import React, { useEffect, useState } from "react";
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import BlogDisplay from '../components/BlogDisplay';
import BlogSkeleton from '../components/BlogSkeleton';
import { globalHistory } from "@reach/router";
import Metadata from '../components/Metadata';
import { BlogPageMetadata } from '../configs/PageMetadata';
import { BlogGetById } from "../services/BlogService";
import { navigate } from 'gatsby';

const useStyles = makeStyles(() => ({
  msgCard: {
    margin: 20,
    padding: 20
  }
}));

export default function BlogPage(props) {
  const classes = useStyles();
  const [blogData, setBlogData] = useState(null);
  const [isEmptyData, setEmptyData] = useState(null);

  var blogId = undefined;
  if (globalHistory.location.hash !== undefined) {
    blogId = globalHistory.location.hash.replace("#", '');
  }
  else {
    if (typeof window !== `undefined`) {
      navigate('/404');
    }
  }
  useEffect(() => {
    if (blogId !== undefined) {
      BlogGetById(blogId)
        .then((response) => {
          if (response !== undefined) {
            setBlogData(response);
          }
          else {
            if (typeof window !== `undefined`) {
              navigate('/404');
            }
          }
        })
        .catch(error => {
          if (typeof window !== `undefined`) {
            navigate('/404');
          }
        });
    }
  }, []);

  return (
    <Layout>
      { (blogData === null) ?
        <BlogSkeleton /> :
        <div>
          <Metadata Title={blogData.title + " | Blog | Bipul's Web"} Description={"Read Bipul's Blog on : '" + blogData.title + "' | Bipul's Web"} BlogId={blogId} />
          <BlogDisplay blog={blogData} />
        </div>
      }
      <Metadata
        Title={BlogPageMetadata.Title}
        Description={BlogPageMetadata.Description}
        BlogId={blogId}
      />
    </Layout>
  );
}